html,
body {
  width: 100%;
  height: 100%; }

#root {
  width: 100%;
  height: 100%; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

ul,
li {
  list-style: none; }

a {
  text-decoration: none; }

input {
  outline: none;
  border: none; }

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px white inset !important; }

.login-form .label-login input, .postEditor input, .postEditor textarea {
  border: 1px solid #ededed;
  border-radius: 5px; }

.login-form .label-login input:focus, .postEditor input:focus, .postEditor textarea:focus {
  border-color: #328bf1; }

.login-form .submit-login {
  color: #fff;
  background-color: #1890ff;
  border-color: #1890ff;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.12);
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.045);
  cursor: pointer;
  border-radius: 5px; }

.login-form .submit-login:hover,
.login-form .submit-login:focus {
  color: #fff;
  background-color: #40a9ff;
  border-color: #40a9ff; }

button {
  border: 1px solid #1890ff;
  background: #1890ff;
  color: #fff;
  padding: 10px 10px 10px 20px;
  letter-spacing: 10px;
  border-radius: 2px;
  cursor: pointer; }
  button:hover {
    background: #40a9ff;
    border: 1px solid #40a9ff; }

.header {
  height: 50px;
  border-top: 3px solid #009a61;
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1), 0 1px rgba(0, 0, 0, 0.1);
  background: #fafafa; }

.header .nav {
  position: relative;
  width: 900px;
  margin: 0 auto; }

.header a {
  color: grey;
  text-decoration: none;
  line-height: 50px; }

.header span {
  line-height: 50px; }

.header .left-link {
  position: absolute;
  left: 0px; }

.header .right-link {
  position: absolute;
  right: 20px; }

.header .user {
  position: absolute;
  right: 20px; }

.header button:hover {
  cursor: pointer; }

.login {
  width: 100%;
  height: 100%;
  background-color: #1890ff;
  overflow: hidden;
  position: relative; }

.login-form {
  width: 300px;
  height: 200px;
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1), 0 1px rgba(0, 0, 0, 0.1);
  padding: 20px;
  position: absolute;
  margin: auto;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
  z-index: 100;
  background: #fff;
  border-radius: 5px; }
  .login-form div {
    margin-top: 10px;
    margin-bottom: 10px; }
  .login-form .label-login {
    height: 40px;
    line-height: 40px; }
    .login-form .label-login span {
      display: inline-block;
      width: 80px;
      text-align: right;
      font-size: 16px;
      font-weight: bolder; }
    .login-form .label-login input {
      height: 30px;
      line-height: 30px;
      width: 160px;
      padding: 0 20px; }
  .login-form .submit-login {
    width: 80px;
    height: 40px;
    display: block;
    background: #1890ff;
    margin: 0 auto;
    cursor: pointer;
    margin-top: 20px; }

.postList {
  width: 900px;
  margin: 0 auto; }

.postItem {
  border-top: 1px solid grey;
  padding: 15px;
  font-size: 14px;
  color: grey;
  line-height: 21px; }

.postItem .title {
  font-size: 16px;
  font-weight: bold;
  line-height: 24px;
  color: #009a61; }

.postItem .like {
  width: 100%;
  height: 20px; }

.postItem .like img {
  width: 20px;
  height: 20px; }

.postItem .like span {
  width: 20px;
  height: 20px;
  vertical-align: middle;
  display: table-cell; }

.postItem textarea {
  width: 100%; }

.postView .mark {
  color: grey;
  margin-bottom: 20px; }

.postView .mark span {
  margin-right: 5px; }

.postView .mark .author {
  font-size: 1.2em;
  font-weight: bold;
  color: #009a61; }

.postView .content {
  color: grey;
  margin-bottom: 20px; }

.postView .vote {
  height: 30px;
  margin-bottom: 20px; }

.postView .vote span {
  vertical-align: middle;
  display: table-cell;
  width: 30px;
  height: 30px; }

.postView .vote img {
  height: 30px;
  width: 30px; }

.postEditor .editor-save {
  float: right; }

.postEditor input {
  margin: 10px auto;
  width: 100%;
  line-height: 1.5em;
  height: 30px;
  line-height: 30px;
  margin-bottom: 20px; }

.postEditor textarea {
  margin-bottom: 10px;
  height: 100px;
  width: 100%; }

.postView .mark {
  color: grey;
  margin-bottom: 20px; }

.postView .mark span {
  margin-right: 5px; }

.postView .mark .author {
  font-size: 1.2em;
  font-weight: bold;
  color: #009a61; }

.postView .content {
  color: grey;
  margin-bottom: 20px; }

.postView .vote {
  height: 30px;
  margin-bottom: 20px; }

.postView .vote span {
  vertical-align: middle;
  display: table-cell;
  width: 30px;
  height: 30px; }

.postView .vote img {
  height: 30px;
  width: 30px; }

.commentsView li {
  padding: 10px 0;
  margin: 10px;
  border-top: 1px dotted grey; }

.commentsView .sub {
  color: grey;
  font-size: 0.8em; }

.commentList {
  border-top: 1px inset grey;
  padding-top: 20px;
  color: black; }
  .commentList .comments {
    height: 50px;
    line-height: 50px;
    font-size: 20px; }

.commentList .title {
  color: grey;
  margin-bottom: 10px; }

.commentList textarea {
  width: 100%;
  height: 50px;
  margin-bottom: 20px; }

.post {
  width: 900px;
  min-height: 900px;
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.1), 0 1px rgba(0, 0, 0, 0.1);
  margin: 10px auto;
  padding: 20px 20px; }

